@font-face {
  font-family: 'Digital';
  src: url('./fonts/Digital-Regular.eot');
  src: url('./fonts/Digital-Regular.eot?#iefix') format('embedded-opentype'),
      url('./fonts/Digital-Regular.woff2') format('woff2'),
      url('./fonts/Digital-Regular.woff') format('woff'),
      url('./fonts/Digital-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
body {
  margin: 0;
  font-family: 'Digital';
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
