.app-wrap{
	width: 100%;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.calculator{
	padding: 5px;
	width:350px;
	background-color:black;
	color:#fff;
}

.formula-screen{
	min-height: 35px;
	padding: 5px 0;
	text-align:right;
	color:#FFAA00;
	word-break: break-all;
}
.output-screen{
	display: flex;
	align-items: center;
	justify-content: flex-end;
	min-height: 35px;
	font-size: 22px;
	text-align: right;
	word-break: break-all;
}

.panel{
	display:grid;
	grid-template-columns:25% 25% 25% 25%;
	grid-template-rows: 65px 65px 65px 65px 65px;
}
.panel-button{
	color:#fff;
	font-size:20px;
	font-family:'Digital';
	background:#7a7c7e;
	border:1px solid #000;
	outline: none;
}
.panel-button.disabled{
	pointer-events: none;
}
.panel-button:focus{
	border:1px solid #fff;
}
#one{
	grid-row-start:2;
	grid-row-end:3;
	grid-column-start:1;
	grid-column-end:2;
}
#two{
	grid-row-start:2;
	grid-row-end:3;
	grid-column-start:2;
	grid-column-end:3;
}
#three{
	grid-row-start:2;
	grid-row-end:3;
	grid-column-start:3;
	grid-column-end:4;
}
#four{
	grid-row-start:3;
	grid-row-end:4;
	grid-column-start:1;
	grid-column-end:2;
}
#five{
	grid-row-start:3;
	grid-row-end:4;
	grid-column-start:2;
	grid-column-end:3;
}
#six{
	grid-row-start:3;
	grid-row-end:4;
	grid-column-start:3;
	grid-column-end:4;
}
#seven{
	grid-row-start:4;
	grid-row-end:5;
	grid-column-start:1;
	grid-column-end:2;
}
#eight{
	grid-row-start:4;
	grid-row-end:5;
	grid-column-start:2;
	grid-column-end:3;
}
#nine{
	grid-row-start:4;
	grid-row-end:5;
	grid-column-start:3;
	grid-column-end:4;
}
#zero{
	grid-row-start:5;
	grid-row-end:6;
	grid-column-start:1;
	grid-column-end:3;
}
#decimal{
	grid-row-start:5;
	grid-row-end:6;
	grid-column-start:3;
	grid-column-end:4;
	font-weight: bold;
}
#reset{
	grid-row-start:1;
	grid-row-end:2;
	grid-column-start:1;
	grid-column-end:2;
}
#delete{
	grid-row-start:1;
	grid-row-end:2;
	grid-column-start:2;
	grid-column-end:3;
}
#divide{
	grid-row-start:1;
	grid-row-end:2;
	grid-column-start:3;
	grid-column-end:4;
}
#multiply{
	grid-row-start:1;
	grid-row-end:2;
	grid-column-start:4;
	grid-column-end:5;
}
#minus{
	grid-row-start:2;
	grid-row-end:3;
	grid-column-start:4;
	grid-column-end:5;
}
#plus{
	grid-row-start:3;
	grid-row-end:4;
	grid-column-start:4;
	grid-column-end:5;
}
#equals{
	grid-row-start:4;
	grid-row-end:6;
	grid-column-start:4;
	grid-column-end:5;
}